	module.exports = {"title":"About Me","date":"2015-05-28T22:40:32.169Z","path":"/about/","subheadline":"Toni Meuschke","image":"me.jpg","locale":"DE","body":"<p>Ich habe die letzten 5 Jahre in Berlin gelebt und als Software Engineer im Bereich JavaScript &amp; Node.js für <a href=\"https://www.lieferando.de\">lieferando.de</a> gearbeitet. Das Team war fantastisch so fantastisch das ich glaube nie wieder in einem so guten Team zu arbeiten. Auch die Projekte steckten voller Abenteuer und Cutting Edge Technologien. Doch trotz alledem habe ich meinen 9 to 5 Job gekündigt um die Welt zu bereisen und ortsunabhängig zu arbeiten.</p>\n<blockquote>Cutting Edge Lover & Javascript Enthusiast</blockquote>\n<p>Warum?\nDafür gab es Tausende Gründe …\nEin sehr ausschlaggebender Punkt ist, dass ich schon immer etwas Eigenes machen wollte.\nAls Angestellter habe ich oft Momente gehabt wie: “Das würde ich anders machen!” oder “Ich glaube nicht, dass diese Strategie erfolgreich sein wird.”.</p>\n<blockquote>Ich kann kein Produkt umsetzten, an das ich nicht zu 100 % glaube!</blockquote>\n<p>Des Weitere habe ich das wirklich unglaubliche Glück als Softwareentwickler ortsunabhängig Arbeiten zu können. Also warum sollte ich jeden Tag von 9-18 Uhr im Büro sitzen, wenn ich in stattdessen die Welt bereisen kann. Dieses Privileg nicht zu nutzen wäre doch unglaublich dumm.\nNatürlich ist nicht alles Gold, was glänzt! Reisen und Arbeiten unter einen Hut zu bekommen ist nicht immer einfach. Nicht funktionierendes WIFI im Hotel ist da noch harmlos …\nDeshalb möchte ich auf diesem Blog einige meiner gesammelten Erfahrungen teilen.</p>\n<div class='center-lg'>\n<iframe src=\"http://www.intagme.com/in/?u=dG9uaW1ldXxpbnwxMDB8NXwyfHx5ZXN8NXx1bmRlZmluZWR8eWVz\" allowTransparency=\"true\" frameborder=\"0\" scrolling=\"no\" style=\"border:none; overflow:hidden; width:575px; height: 230px\" ></iframe>\n</div>\n<h6>Newsletter</h6>\n<p><a href=\"http://tech.us13.list-manage.com/subscribe?u=5c9ee910a162ad82d9717de48&id=a9ca9d8016\" target=\"_blank\">Hier kannst du dich für den Newsletter anmelden um Neuigkeiten per E-Mail zu bekommen.</a></p>\n<h6>Connect</h6>\n<p><a href='https://www.xing.com/profile/Toni_Meuschke' target='_blank' class='icon fa-xing' title=\"Xing\"></a>\n<a href='https://de.linkedin.com/in/tonimeuschke' target='_blank' class='icon fa-linkedin' title=\"LinkedIn\"></a>\n<a href='https://github.com/meuschke' target='_blank' class='icon fa-github' title=\"Github\"></a>\n<a href=\"https://www.instagram.com/tonimeu/\" target=\"_blank\" class=\"icon fa-instagram\" title=\"Instagram\"></a>\n<a href=\"https://docs.google.com/forms/d/1Q-pt8vxPGWPRaaObv1nn2BpK1gDmqq1YX2RldZgvdFc/viewform\" target=\"_blank\" class=\"icon fa-envelope\" title=\"E-Mail\"></a></p>\n"}

