	module.exports = {"title":"Packliste","date":"2016-04-21","layout":"post","path":"/digitale-nomaden-packliste/","subheadline":"Was ist in meinem Backpack?","description":"Das habe ich für meine Weltreise und das ortsunabhängiges Arbeiten eingepackt.","image":"packliste.jpg","locale":"DE","body":"<p>Die größte Frage, die sich einem vor Abreise stellt, ist meist “Was packe ich ein?”.\nGrundsätzlich sollte man sich bei jedem Teil fragen wie oft man es braucht, ob es notwendig ist und wenn ja, ob es möglicherweise leichtere oder kleinere Alternativen gibt.\nEs empfiehlt sich auch während der Reise (nach ca. 4 Wochen) erneut die gleiche Frage zu stellen.\nEs gibt Nomaden die nur mit Handgepäck reisen. Ich allerdings habe mich dafür entschieden einen Backpack zu nutzen und über den Zeitraum meiner Reise Stück für Stück minimaler zu leben.\nIm Moment reise ich mit ca. 13 kg. Ich möchte aber mein Gepäck auf 10 kg minimieren.</p>\n<h3>Rucksack</h3>\n<p>Wenn man sich für einen Rucksack entscheidet, sollte man darauf achten, dass dieser sich von oben wie ein Koffer öffnen lässt.\nSomit muss man nicht immer den kompletten Rucksack auspacken, wenn man etwas von ganz unten braucht.\nIch habe mich für den <a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00H8BLNKK/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&linkCode=as2\">Jack Wolfskin HIGHLAND TRAIL XT 50 Rucksack</a> entschieden und bin begeistert.\nEr hat ein großes sowie ein kleineres Fach. Das große lässt sich von oben sowie von der Seite des Rucksacks bepacken.\nDas etwas kleinere Fach befindet sich unten. Zusätzlich bieten einige kleine Fächer Stauraum für Ladegeräte und Kabel.</p>\n<div class='row center-xs'>\n<div class='col-xs-12 col-md-5'>\n<img src='./backpack.jpg' alt='Garkueche in Bangkok - Bohnen, Reis, Tofu' title='' />\n</div>\n</div>\n<br/>\n<p>Wer minimaler reisen möchte, dem empfehle ich den <a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00M0P1TUI/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&linkCode=as2\">Cabin Max Oxford Rucksack</a>.</p>\n<h3>DayPack</h3>\n<p>Einen zweiten Rucksack und/oder einen Jutebeutel sollte man für Tagesausflüge oder Supermarkteinkäufe mitnehmen.\nIch habe einen Jutebeutel, den man auch als Rucksack nutzen kann und einen schwarzen einfachen Rucksack von Topman, der sich klein zusammenfalten lässt.</p>\n<div class='row center-xs'>\n<div class='col-xs-12 col-md-5'>\n<img src='./daybag.jpg' alt='DayPack - Topman' title='' />\n</div>\n</div>\n<br/>\n<h3>Pacsafe</h3>\n<p>Unerlässlig wenn man mit teuren Geräten reist! Der Pacsafe ist ein portabler Safe in den ein 13’’ Macbook sowie alle meine wichtigen Sachen wie <a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00O32GGTK/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00O32GGTK&linkCode=as2&tag=wanderlust07-21\">GoPro</a>, Reisepass, Sony RX100 und Ipad passen.\nIch habe mich für die 5 L Variante entschieden.</p>\n<div class='row center-xs'>\n<div class='col-xs-12 col-md-5'>\n<img src='./pacsafe.jpg' alt='Pacsafe' title='' />\n</div>\n</div>\n<br/>\n<p>Es gibt auch einen\n<a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00KU52GU2/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&linkCode=as2\">Pacsafe Rucksack</a>.</p>\n<h3>Technisches Equipment</h3>\n<ul>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00MVZ89O6/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00MVZ89O6&linkCode=as2&tag=wanderlust07-21\">  Macbook 13’’ Retina </a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00G9T8ZWG/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00G9T8ZWG&linkCode=as2&tag=wanderlust07-21\"> Ipad Mini</a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00F8JDD2K/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00F8JDD2K&linkCode=as2&tag=wanderlust07-21\">  Iphone 5s 32 GB </a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00O32GGTK/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00O32GGTK&linkCode=as2&tag=wanderlust07-21\">GoPro Hero 4 Black</a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00DM8R866/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00DM8R866&linkCode=as2&tag=wanderlust07-21\">Sony Rx 100</a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00009ZSYG/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00009ZSYG&linkCode=as2&tag=wanderlust07-21\">Sennheiser HD 25-1 II Kopfhörer</a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00QJDO0QC/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B00QJDO0QC&linkCode=as2&tag=wanderlust07-21\">Kindle Paperwhite</a></li>\n<li>HTC Desire 510</li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B014D1N0CG/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B014D1N0CG&linkCode=as2&tag=wanderlust07-21\"> Bluetooth Stick mit Stativ</a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B01422TC14/ref=as_li_tl?ie=UTF8&camp=1638&creative=6742&creativeASIN=B01422TC14&linkCode=as2&tag=wanderlust07-21\">Power Bank</a></li>\n</ul>\n<h3>Sonstiges</h3>\n<ul>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B000UUOVEG/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&creativeASIN=B000UUOVEG&linkCode=as2&tag=wanderlust07-21\">Vakuumbeutel für Kleidung</a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B004FVA5YG/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&creativeASIN=B004FVA5YG&linkCode=as2&tag=wanderlust07-21\">Frischhaltedose 1L</a></li>\n<li>Mückenspray 50% DEET (<a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00E65VHB6/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&creativeASIN=B00E65VHB6&linkCode=as2&tag=wanderlust07-21\">Kleidung</a> &amp; <a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00E66ZOKU/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&creativeASIN=B00E66ZOKU&linkCode=as2&tag=wanderlust07-21\">Haut</a>)</li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00PW155ZM/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&creativeASIN=B00PW155ZM&linkCode=as2&tag=wanderlust07-21\"> Kofferwaage</a></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B00J250F6I/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&creativeASIN=B00J250F6I&linkCode=as2&tag=wanderlust07-21\">Campingbesteck</a></li>\n<li>Notizbuch</li>\n<li>Koh Lanta Trash Hero Flasche (<a href=\"http://www.trashhero.org\" target=\"_blank\">www.trashhero.org</a>) <sup>*</sup></li>\n<li><a rel=\"nofollow\" href=\"http://www.amazon.de/gp/product/B014HK1F5M/ref=as_li_tl?ie=UTF8&camp=1638&creative=19454&creativeASIN=B014HK1F5M&linkCode=as2&tag=wanderlust07-21\">Jack Wolfskin Kulturbeutel Waschsalon</a></li>\n</ul>\n<p><sup>*</sup> <small>Habe ich mir unterwegs gekauft.</small></p>\n"}

