	module.exports = {"title":"Bangkok","date":"2016-04-22","layout":"post","path":"/digitale-nomaden-tipps-bangkok/","image":"bangkok.jpg","description":"กรุงเทพฯ ist die Hauptstadt von Thailand und liegt am Fluss Mae Nam Chao Phraya.","subheadline":" ","locale":"DE","body":"<p>Bangkok ist für die meisten Reisenden die erste Stadt in Asien, da die Flüge aus Deutschland in diese Metropole am günstigsten sind.\nWenn asiatischen Großstädte nicht gewohnt ist, kann einem Bangkok schon ganz schön umhauen!\nAlles ist ständig in Bewegung und es prasseln Tausende Eindrücke auf einen ein.</p>\n<h3>How to get in?</h3>\n<p>Landet man am Suvarnabhumi Airport (BKK) kann man beruhigt ein Taxi in die Stadt nehmen.\nNach Chinatown kostet es ca. 350 THB.</p>\n<p>Es gibt aber auch die Möglichkeit den Skytrain zu nutzen. Der Express Zug (15 min) kostet 100 THB und die Bimmelbahn (30 min) zwischen 15-40 THB.</p>\n<h3>How to get around?</h3>\n<p>Tuk-Tuk, Motorradtaxi, Bus, Taxi, Boot, Skytrain, Metro oder Uber, das ist hier die Frage!</p>\n<h5>Tuk-Tuk</h5>\n<p>Sie sind Fluch und Segen zugleich!\nEs macht unglaublich Spaß in einem durch Bangkok zu düsen, jedoch muss man sich immer wieder mit Schleppern oder überteuerten Preisen herumschlagen.\nAchtet immer darauf, dass das Tuk-Tuk ein gelbes Kennzeichen hat! Es gibt Schlepper, die euch in vermeintliche Touristeninformationen fahren, wo ihr dann Tickets für irgendetwas kaufen sollt.</p>\n<p>Grundsätzlich ist handeln Pflichtprogramm! Eine kurze Strecke für ca. 2 km sollte zwischen 40-50 Bath kosten.\nDer Fahrer wird euch einen Preis von 100 THB vorschlagen. Wenn ihr euch auf 75 % des ursprünglichen Fahrpreises einigt, habt ihr eine angenehme Fahrt. Handelt ihr ihn auf 50 % wird die Fahrt etwas riskanter :)\nScheut euch auch nicht ein neues Tuk-Tuk anzuhalten, wenn der erste Fahrer nicht mit sich handeln lässt.</p>\n<h6>Tipp:</h6>\n<ul>\n<li>Meiden sollte man Tuk-Tuks in der Rushhour (7:00-9:00 &amp; 16:00-19:00).</li>\n<li>Ein Thai hat uns gesagt, dass man einfach <i>Phæng Phai (zu teuer)</i> sagt, wenn einem der Preis zu teuer. Danach sagt man einen neuen Preis.</li>\n</ul>\n<div class='row center-xs'>\n<div class='col-xs-12 col-md-6'>\n<img src='./tuk-tuk.jpg' alt='Tuk-Tuk in Bangkok' title='' />\n</div>\n</div>\n<br/>\n<h5>Taxi</h5>\n<p>Taxis haben ein Taximeter-Schild auf dem Autodach sowie ein gelbes Kennzeichen und eine Lizenz links an der Windschutzscheibe. Einfach Hand raus und los gehts! Achtet darauf, dass das Taxi-Meter auch angemacht wird!\nDer erste Kilometer kostet 35 Bath, danach jeder weitere 5 Baht.</p>\n<p>Tipp: Es ist immer gut die Adresse in Thai auf einem Zettel zu haben.</p>\n<h5>Uber</h5>\n<p>Die wohl komfortabelste und einfachste Art in Bangkok von A nach B zu kommen.\nUber App auf Zielort eingegeben und ein Uber-Taxi request absenden.\nDie Base Fare liegt bei 25 Bath und der km bei ca 5 Bath.\nDas Gute ist, ihr braucht kein Bargeld und müsst dem Fahrer nicht stundenlang erklären, wo es hingehen soll…</p>\n<div class='row center-xs'>\n<div class='col-xs-12 col-md-5'>\n<img src='./uber-app.jpg' alt='Uber in Bangkok' title='' height='400' />\n</div>\n</div>\n<br/>\n<h5>Boot</h5>\n<p>Der Chao Phraya ist voll mit Expressbooten, deren Fahrt maximal 31 Baht pro Person kosten.\nDie Boote haben verschieden Flaggen, die für die Linie stehen.\nAm Einstiegsort befindet sich ein Schild mit welcher Linie welche Station anfährt.</p>\n<h5>Skytrain</h5>\n<p>Das Skytrain Tagesticket kostet ca. 130 Bhat und ihr kommt damit an alle wichtigen Orte Bangkoks.</p>\n<h3>How to connect ?</h3>\n<p>Wenn man in Bangkok angekommen ist, sollte man sich eine Prepaid Simkarte mit mobilem Internet kaufen.\nIch kann die TrueMove H empfehlen. Diese gibt es für 79 THB in jedem 7-eleven und sie enthält 750 MB 4G Datenvolumen.\nMan braucht für die Aktivierung im 7-eleven seinen Reisepass. Für 399 THB gibt es zusätzlich 3 GB Datenvolumen.\nLeider ist es nicht möglich online über <a href=\"http://trueiservice.com\">trueiservice.com</a> Datenvolumen zu kaufen. Nach Anfrage beim Support hieß es, dass nur thailändische Kreditkarten akzeptiert werden.</p>\n<p>Tipp: Zur Aktivierung der Karte muss man die Nummer auf dem Kassenzettel eingeben. Diese beginnt mit einer #.</p>\n<p>Zusätzlich bietet True auch WIFI Hotspots an. Diese sind sehr gut ausgebaut und man findet die Hotspots an fast jeder Ecke.\nHier gibt es eine Karte: <a href=\"http://www.truewifi.net/wifi/findhotspot\">http://www.truewifi.net/wifi/findhotspot</a>.</p>\n<h3>What to see?</h3>\n<p>Es lohnt sich eine der Tempelanlagen zu besuchen. Besonders schön sind die Tempel Wat Arun (Tempel der Morgendämmerung) &amp; Wat Pho (Tempel des liegenden Buddhas).\nDer Wat Pho kostet 100 Baht, für die man den 46 m langen liegenden Budda bestaunen kann. Auch der Rest der Tempelanlage ist sehr schön.\nDer Wat Arun liegt auf der anderen Seite des Chao Phraya. Dieser besteht aus einem 79 m hohen Turm, den man schon von der anderen Seite des Flusses gut sieht. Nachts erleuchtet der Tempel in seiner vollen Pracht.</p>\n<p>Tipp: Man muss in den Tempelanlagen angemessene Kleidung tragen, das heißt bedeckte Schultern und lange Hose. Diese kann man sich auch für 30 Bath am Tempel ausleihen.</p>\n<h3>Where to eat?</h3>\n<p>Bangkok bietet Tausende Garküchen an jeder Ecke gibt es etwas Leckeres und Günstiges zum Essen.\nDie Besten findet man angeblich in Chinatown.</p>\n<p><h5>Garküche an der Soi Ram Butri</h5></p>\n<div class='row center-sm'>\n<div class='col-xs-12 col-md-5 '>\n<img src='./garkueche.jpg' alt='Garkueche in Bangkok - Bohnen, Reis, Blumekohl' title='' />\n</div>\n<div class='col-xs-12 col-md-5'>\n<img src='./garkueche2.jpg' alt='Garkueche in Bangkok - Bohnen, Reis, Tofu' title='' />\n</div>\n</div>\n<br/>\n<h5>ร้านลุง (Lung) <a href=\"http://4sq.com/8Dn0KQ\" class=\"icon fa-foursquare\"></a></h5>\n<p>Besichtigt man den Wat Arun, lohnt es sich ein paar Ecken weiter im Lung zu essen.\nDort gibt es eine fantastische Noodle Soup!</p>\n<div class='row center-sm'>\n<div class='col-xs-12 col-md-5 '>\n<img src='./lung.jpg' alt='Garkueche in Bangkok - Bohnen, Reis, Blumekohl' title='' />\n</div>\n<div class='col-xs-12 col-md-5'>\n<img src='./thai-essen.jpg' alt='Garkueche in Bangkok - Bohnen, Reis, Tofu' title='' />\n</div>\n</div>\n<br/>\n<h3>Where to go out?</h3>\n<p>Eine Bar oder einen Club zu finden sollte in Bangkok kein Problem darstellen.\nAn fast jeder Ecke kann man etwas trinken und auch unter der Woche haben die Clubs auf.\nEine Liste der Clubs findet man auf <a href=\"https://www.residentadvisor.net/events.aspx?ai=67\">Residentadvisor</a>.\nWas man aber unbedingt gemacht haben muss, ist eine der Skybars zu besuchen.</p>\n<h6>Moonbar <a href=\"http://4sq.com/fQMu3V\" class=\"icon fa-foursquare\"></a></h6>\n<p>Die Moonbar befindet sich im 61. Stockwerk des Banyan Tree Hotel und bietet einen atemberaubenden Blick über die Stadt.\nAm besten fährt man mit einem Uber dort hin, da man so auch direkt am Eingang im Hinterhof abgesetzt wird.\nAuf dem Rooftop befindet sich ein Restaurant mit Bar. Ein Mojito kostet ca. 500 THB dazu bekommt man zu jedem Getränk Snacks.\nNatürlich solltet man auch in angemessener Kleidung erscheinen!</p>\n<p>Tipp: Man sollte nicht zu Berliner Zeiten dort aufkreuzen, da die letzte Runde ca. 00:30 Uhr ist.</p>\n<h3>Where to sleep?</h3>\n<p>Es gibt unendliche viele Übernachtungsmöglichkeiten. Es lohnt sich immer wider zu vergleichen und Bewertungen zu lesen.\nIch nutze dafür Booking, Trivago, Airbnb und Skyscanner.</p>\n<h6>Loftel 22 Hostel <a href=\"http://4sq.com/1c9Okx1\" class=\"icon fa-foursquare\"></a></h6>\n<p>Ein Hostel, das ich sehr empfehlen kann! Es bietet nicht nur Schlafsäle, sondern auch Einzelzimmer. Dazu ist es sehr modern, das Personal ist freundlich und in der Lobby befindet sich ein Café, in dem man frisch gemahlenen Kaffee und Frühstück bekommt.</p>\n<h3>Good to know!</h3>\n<h6>Alkohol</h6>\n<p>Alkoholische Getränke dürfen täglich nur von 11 bis 14 Uhr sowie von 17 bis 24 Uhr verkauft werden.</p>\n<h6>Thai Reise-Wortschatz</h6>\n<p>Wenn man ein paar Vokabeln kann, bekommt man immer ein nettes Lächeln von den Thais.</p>\n<p><i>Hallo</i><br/>\nสวัสดีครับ, สวัสดีค่ะ\nsàwàddee kráb, sàwàddee ká</p>\n<p><i>Danke</i><br/>\nขอบคุณครับ, ขอบคุณค่ะ\nkòbkûn kráb, kòbkûn ká - Danke</p>\n<p><i>Prost!</i><br/>\nไชโย\nchái yó</p>\n"}

