	module.exports = {"title":"Travel Apps","date":"2016-04-21","layout":"post","path":"/digitale-nomaden-travel-apps/","subheadline":" ","description":"Apps & Tools für Digitale Nomaden & Freelancer, die dir bei deiner nächsten Reise helfen.","image":"travel-apps.jpg","locale":"DE","body":"<p>Digitale Services und Apps erleichtern uns das Leben. Egal ob man bloß mal schnell das beste Café um die Ecke finden möchte oder eine Unterkunft für den nächsten Monat in Hongkong. Besonders für Digitale Nomaden &amp; ortsunabhängige Freelancer sind diese Apps ein must have!</p>\n<h3><u>From A to B</u></h3>\n<h5><a href=\"http://www.skyscanner.de/\">Skyscanner</a></h5>\n<p>Mit Skyscanner kann man unglaublich gut Flüge vergleichen. Der Vorteil dieser Plattform ist, dass man keinen Zielort angeben muss und somit alle möglichen Orte vergleichen kann. Dazu kann man auch ganze Monate auswählen und Skyscanner zeigt einem den günstigsten Preis an.</p>\n<p>In der Regel bucht man 1 Monat im Voraus am günstigsten. Flüge am Mittwoch sind meist preiswerter als am Wochenende.</p>\n<h5><a href=\"https://www.uber.com/\">Uber</a></h5>\n<p>I ♥ Uber! Mit der App kann man ein Taxi anfordern bezahlt wird das ganze Bargeldlos über Kreditkarte.\nMan kann den Zielort eingeben was besonders im Ausland sehr praktisch ist, da ich dem Fahrer nicht erklären muss wo ich hin will.</p>\n<h3><u>Unterkunft</u></h3>\n<h5><a href=\"https://www.airbnb.de/\">Airbnb</a></h5>\n<p>Airbnb nutze ich für längere Aufenthalte. Bucht man Unterkünfte für 1 Monat bekommt man meist Rabatt.</p>\n<p>Tipp: <a href=\"https://www.airbnb.de/c/tonim159?s=8\">Hier gibt es kostenlos 31€ Reiseguthaben!</a></p>\n<h5><a href=\"https://www.booking.com\">Booking</a></h5>\n<p>Booking nutze ich für kurze Aufenthalte in Hotels oder Hostels.</p>\n<p>Tipp: Bucht man 10 Mal über die Plattform, wird man genius Mitglied und erhält super Rabatte.</p>\n<h5><a href=\"http://couchsurfing.com\">Couchsurfing</a></h5>\n<p>Couchsurfing ist fantastisch, wenn man mal Tipps von Locals bekommen möchte und das Geld knapp ist.</p>\n<h5><a href=\"http://hostelworld.com\">Hostelworld</a></h5>\n<p>Die besten Hostels findet man auf Hostelworld.</p>\n<h3><u>Places</u></h3>\n<h5><a href=\"http://Foursquare.com\">Foursquare</a></h5>\n<p>Mein täglicher Begleiter auf der Suche nach Restaurants, Cafés oder anderen Orten.</p>\n<h5><a href=\"http://workhardanywhere.com\">Work Hard Anywhere</a></h5>\n<p>Wer einen Platz zum Arbeiten sucht wird mit dieser App fündig.</p>\n<p>Tipp: Auch wenn der Macbook Akku mal wieder leer ist, kann man hier nach Cafés mit Steckdosen filtern.</p>\n<h5><a href=\"https://play.google.com/store/apps/details?id=nl.npsoftware.speedster\">Speedtest</a></h5>\n<p>Brauch man mal schnelles WIFI in der Umgebung ist diese App die Rettung.</p>\n<h5><a href=\"https://nomadlist.io\">Nomadlist</a></h5>\n<p>Unklar wo es als nächstes hingehen soll?\nMit Nomadlist kann man die besten Städte für Digitale Nomaden finden.</p>\n<h5><a href=\"http://wikitravel.org\">Wikitravel</a></h5>\n<p>Das Wikipedia für Reisende! Man findet alle möglichen Informationen über Städte und Länder.</p>\n<h5><a href=\"http://www.sleepinginairports.net/\">Sleepinginairports</a></h5>\n<p>Wenn der Aufenthalt am Flughafen doch mal wieder länger als geplant ist.\nDiese Seite hilft dir Schließfächer, Restaurants oder andere Orte am Flughafen zu finden.</p>\n<h3><u>Learning &amp; News</u></h3>\n<h5>Duolingo</h5>\n<p>Will man zB. sein Spanisch auffrischen oder neu erlernen hilft Duolingo.\nDie kostenlose Sprachlern-App nutzt super Methoden damit das neu Erlernte auch im Gedächtnis bleibt.</p>\n<h5><a href=\"https://medium.com\">Medium</a></h5>\n<p>Zum Kaffee am morgen gehört nicht die Zeitung sondern Medium!</p>\n<h5>Feedly</h5>\n<p>Der beste News &amp; Rss reader.</p>\n<h3><u>Organisation</u></h3>\n<h5>Trello</h5>\n<p>Ob Projektmanagement oder private To-Do Listen!</p>\n<h5>Google Drive</h5>\n<p>Der beste Cloud Storage, egal ob Dokumente oder das Backup für meinen Mac.</p>\n<h5>Dropbox</h5>\n<p>Als Alternative für Google Drive.</p>\n<h5>CamScanner Pro</h5>\n<p>Perfekt um unterwegs Dokumente einzuscannen!</p>\n<p>Tipp: Mit OCR Erkennung kann man die Dokumente nach Text durchsuchen.</p>\n<h3><u>Navigation</u></h3>\n<h5>Google Maps</h5>\n<p>Sorry, aber ich kann keine analoge Karte mehr lesen…</p>\n<h5>Viewranger</h5>\n<p>Die Karten von Google sind gut aber ich habe manchmal die Erfahrung gemacht das OpenStreet Maps besser sind.\nDie App ist auch perfekt zum Wandern.</p>\n<h5>CityMaps2Go</h5>\n<p>Offline Maps und City Guides.</p>\n<h3><u>Communication</u></h3>\n<h5>Hangouts</h5>\n<p>Perfekt für Kundenkommunikation.</p>\n<h5>Skype</h5>\n<p>Als Alternative für Google Hangouts.</p>\n<h5>Slack</h5>\n<p>“Slack brings all your communication together in one place.”</p>\n<h3><u>Automation</u></h3>\n<h5>IFTTT</h5>\n<p>Automation für alle online Services.</p>\n<h5>Zapier</h5>\n<p>Ähnlich wie IFTTT.</p>\n<h3><u>Finanzen</u></h3>\n<h5>Finanzblick</h5>\n<p>Meine Kontobewegungen im Überblick egal ob Paypal oder DKB.</p>\n<h5>Tripcoin</h5>\n<p>Reisebudget &amp; Währungsrechner mit Dropbox Synchronisation.</p>\n<h3><u>Sonstiges</u></h3>\n<h5>Arq backup</h5>\n<p>Backup Software für Mac (Google Drive, Dropbox, S3, SFTP usw.)</p>\n<h5>Google Photo</h5>\n<p>Unendlicher kostenloser Cloud-Speicher für Fotos.</p>\n<p>Tipp: Der Auto Awesome Assistent erstellt automatisch Filme, Animationen oder Collagen aus euren Bildern.</p>\n<h5>E-Post</h5>\n<p>Für 14,99 € im Monat kann man seine Post digitalisieren lassen.</p>\n<h5>Dict</h5>\n<p>Der Offline Übersetzer schlechthin.</p>\n<h5>Google Translate</h5>\n<p>Online Übersetzter mit Bilderkennung.</p>\n<h5>Wifi Map &amp; Wifi Finder</h5>\n<p>Alle öffentlichen Wifi &amp; Passwörter auf einer Karte angezeigt.</p>\n<h5>SIM WIKI</h5>\n<p>Wiki über Prepaid SIM Karten in verschiedenen Ländern.</p>\n<h5>Macpass &amp; MiniKeePass</h5>\n<p>Kostenloser Open Source Passwortmanager.</p>\n<h5>Smartsteuer</h5>\n<p>Einfach die nächste Steuererklärung online abgeben.</p>\n<h3><u>Software Engineering</u></h3>\n<h5>Dash</h5>\n<p>Software für offline API Dokumentation.</p>\n<h5>iTerm</h5>\n<p>Der bessere Mac Terminal.</p>\n<h5>Atom</h5>\n<p>Der beste Code Editor den ich kenne.</p>\n<h5>Oh-My-Zsh</h5>\n<p>Die besser Bash Shell für den Mac.</p>\n<h5>Cloudflare</h5>\n<p>Kostenloses CDN mit HTTPS.</p>\n<h5>Github</h5>\n<p>Static Site Hosting &amp; Code Versionskontrolle.</p>\n"}

